// 
import {AUTH_NAME} from "@/store/types"//

import  master from "components/master"//
let index = (resolve) => resolve(require('views/agent/index/index'));
let ptcommission = (resolve) => resolve(require('views/agent/ptcommission'));
let subuser = (resolve) => resolve(require('views/agent/subuser'));
let platform = (resolve) => resolve(require('views/agent/platform'));
let creditlog = (resolve) => resolve(require('views/agent/creditlog'));
let profile = (resolve) => resolve(require('views/agent/profile'));
let personal = (resolve) => resolve(require('views/agent/personal'));
export default [
  {
    path: "/agent/index",
    name: 'agent_index',
    component: index,
    meta: {
      auth: [AUTH_NAME.AGENT] //
    }
  },
  {
    path: "/agent",
    name: "agent",
    component: master,
    meta: {
      auth: [AUTH_NAME.AGENT]//
    },
    children: [
      {
        path: "ptcommission",
        name: "ptcommission",
        component: ptcommission,
        meta: {
          headName: ""
        }
      }, {
        path: "subuser",
        name: "subuser",
        component: subuser,
        meta: {
          headName: ""
        }
      }, {
        path: "platform",
        name: "platform",
        component: platform,
        meta: {
          headName: ""
        }
      }, {
        path: "creditlog",
        name: "creditlog",
        component: creditlog,
        meta: {
          headName: ""
        }
      }, {
        path: "profile",
        name: "profile",
        component: profile,
        meta: {
          headName: ""
        }
      }, {
        path: "personal",
        name: "personal",
        component: personal,
        meta: {
          headName: "/"
        }
      }
    ]
  }
]



