import vueRouter from '@/router'; //vue
import _axios from 'axios'; //axios
import store from '@/store';	//
var ajax=_axios.create({
  baseURL:"http://112.213.126.135:4777",
  method:"get",
  timeout:10000,
  responseType:"json",
  withCredentials:true,  // Token
  // cancelToken: new CancelToken(function(cancel){})
  headers:{"Content-Type":"application/x-www-form-urlencoded"}
  // transformRequest: [function (data) {
  //   // form-dataQsaxios
  //   data = Qs.stringify({});
  //   return data;
  // }],
  //
  // transformResponse: [function (data) {
  //   // 
  //
  //   return data;
  // }],
});
// 
var qs=require('qs');
ajax.interceptors.request.use(function(config){
  // console.log("",config);
  config.data=qs.stringify(config.data)
  return config;
},function(error){
  console.log(":"+error);
  return Promise.reject(error);
});
//
ajax.interceptors.response.use(function(response){
  if(response.status===200&& !response.data.hasOwnProperty("success")){
    response.data.success=response.data.code=="10000";
  }
  $load.close();
  if(response.status===200&&response.data.code=="40001"){//
    store.dispatch("REMOVE_AUTH");
    response.data.message=undefined;
    if(!/ajaxGetSessionPersonalData\.php$/i.test(response.config.url)){
      $confirm("...","",{
        confirmText:""
      }).then(()=>{
        vueRouter.push("/login/index");
      })
    }
  }
  return response.status===200 ? response.data : response;
},function(error){
  $load.close();
  return Promise.reject(error);
});
export default ajax;



