import {AUTH_NAME} from "@/store/types"
import  master from "components/master" //
let index=(resolve) => resolve(require('user/index/index'));
let message=(resolve) => resolve(require('views/common/message')); //
let history=(resolve) => resolve(require('views/user/history'));//
let personal=(resolve) => resolve(require('views/user/personal'));  //
let security=(resolve) => resolve(require('views/user/security'));  //
export default [
  {
    path:"/user/index",
    name:"user_index",
    component:index,
    meta:{
      auth:[AUTH_NAME.USER]//
      ,title:""
    }
  },
  {
    path:'/user',
    component:master,
    redirect:"/user/index",
    meta:{
      auth:[AUTH_NAME.USER]
    },
    children:[
      {
        path:"personal",
        name:"user_personal",
        component:personal,
        meta:{
          headName:""
        }
      },
      {
        path:"security",
        name:"user_security",
        component:security,
        meta:{
          headName:""
        }
      },
      {
        path:"history",
        name:"user_history",
        component:history,
        meta:{
          headName:""
        }
      },
      {
        path:"message",
        name:"user_message",
        component:message,
        meta:{
          headName:"",
          auth:[AUTH_NAME.USER,AUTH_NAME.AGENT] //
        }
      }
    ]
  }
]



