import agent from './agent';
import user from './user';
import common from './common';
import {AUTH_NAME} from "@/store/types"//
let index=(resolve) => resolve(require('views/show/index'));//
let history=(resolve) => resolve(require('views/show/history'));
let preferential=(resolve) => resolve(require('views/show/preferential'));



import  login from "views/auth"//
let login_index=(resolve) => resolve(require('views/auth/login'));//
let login_register=(resolve) => resolve(require('views/auth/register-user'))//
let agent_login=(resolve) => resolve(require('views/auth/login')); //
let agent_register=(resolve) => resolve(require('views/auth/register-agent'))//
let register_success=(resolve) => resolve(require('views/auth/register-success'))//


export default [
  {
    path:"/index",
    name:"index",
    component:index,
    meta:{
      noCache:true, //,
      meta:{
        title:""
      }
    }
  },
  {
    path:"/history",
    name:"history",
    component:history,
    meta:{
      title:""
    }
  },
  {
    path:"/preferential",
    name:"preferential",
    component:preferential,
    meta:{
      title:""
    }
  },
  {
    path:"/login",
    name:"login",
    component:login,
    redirect:"/login/index",
    meta:{
      needFalseLogin:true //
    },
    children:[
      {
        path:"index",
        name:"login_index",
        component:login_index,
        meta:{
          title:""
        }
      },
      {
        path:"register",
        name:"register",
        component:login_register,
        meta:{
          title:""
        }
      },
      {
        path:"registersuccess",
        name:"registersuccess",
        component:register_success,
        meta:{
          title:""
        }
      },
      {
        path:"agentLogin",
        name:"agentLogin",
        component:agent_login,
        meta:{
          title:""
        }
      },
      {
        path:"agentregister",
        name:"agentregister",
        component:agent_register,
        meta:{
          title:""
        }
      }
    ]
  },
  ...agent,
  ...user,
  ...common,
  {
    path:"/",
    redirect:"/index"
  }
]



