<template>
  <div class="page_content_wrap">
    <div class="layout_form layout_form03" style="margin-bottom: 1em;">
      <form action="#">
        <div class="form_field_warp">
          <div class="form_field">
            <span class="form_field_label"></span>
            <div class="form_field_input">
              <select v-model="searchData.historyType">
                <option value="deposit"></option>
                <option value="withdraw"></option>
                <option value="transfer"></option>
                <option value="concessionReccords"></option>
                <option value="couponRecords"></option>
                <option value="deputyRecords"></option>
                <option value="ximaDetail"></option>
                <option value="friend"></option>
              </select>
            </div>
          </div>
          <div class="form_field" v-show="searchData.historyType=='friend'">
            <span class="form_field_label"></span>
            <div class="form_field_input">
              <select v-model="searchData.friendtype">
                <option value="0"></option>
                <option value="1"></option>
                <option value="2"></option>
              </select>
            </div>
          </div>
          <div class="form_field">
            <span class="form_field_label"></span>
            <div class="form_field_input">
              <select v-model="Days">
                <option value="1"></option>
                <option value="7"></option>
                <option value="30"></option>
              </select>
            </div>
          </div>
        </div>
        <div class="form_btn_wrap">
          <a href="javascript:;" @click="search" class="btn btn01"></a>
        </div>
      </form>
    </div>
    <table-data :thead="thead" :data="data">
      <tr v-for="(item,i) in data.records">
        <td>{{item.pno}}</td>
        <td>{{item.amount}}</td>
        <td>{{item.flag}}</td>
        <td>{{item.tempCreateTime}}</td>
      </tr>
    </table-data>
  </div>
</template>
<script>
  import  {queryHistory} from "api/user";
  import tableData from "components/table-data"
  export default {
    data() {
      return {
        thead:['','','',''],
        data:{},
        Days:"1",
        searchData:{
          historyType:"deposit",
          startDate:"",
          endDate:'',
          pageIndex:1,
          size:10,
          friendtype:"0"
        }
      };
    },
    watch:{
      Days(val){
        this.searchData.endDate=(new Date().addDay(val* -1).format("yyyy-MM-dd"));
      },
      "searchData.historyType"(val){
        if(val=="friend"){
        }
      }
    },
    methods:{
      search(){
        this.showData= !this.showData;
        queryHistory(this.searchData).then(data =>{
          if(data.success){
            this.data=data.data;
          }else{
            toast(data.message)
          }
        })
      }
    },
    created(){
      this.searchData.startDate=new Date().format("yyyy-MM-dd");
      this.Days=1;
    },
    components:{
      tableData
    }
  };
</script>
<style>
</style>



