<template>
  <div id="page_content" class="flex_1">
    <div class="page_content_wrap">
      <div class="sec_box layout_transfer_account">

        <div id="j-top" class="select-top">
          <div class="to_account display_flex_h flex_align_center">
            <div class="label">
              <span class="label-text text_red"></span>
              <span></span>
            </div>
            <div class="flex_1">
              <div id="credit" data-f="0.0 " data-z="0">0 </div>
              <select class="without_style with_arrow select j-moneyAccount">
                <option value="MAIN"></option>
                <option value="DEPUTY"></option>
              </select>
              <input type="hidden" class="hd-value" value="MAIN">
            </div>
            <div id="transfer-source-credit" class="balance-txt"></div>
          </div>
        </div>
        <div id="j-sub">
          <div class="to_account display_flex_h flex_align_center">
            <div class="label">
              <span class="label-text text_green"></span>
              <span></span>
            </div>
            <div class="flex_1">
              <select class="without_style with_arrow hd-value select" id="transfer-target">
                <option value=""></option>
                <option value="PT">PT</option>
                <option value="MG">MG</option>
                <option value="TTG">TTG</option>
                <option value="NT">NT</option>
                <option value="QT">QT</option>
                <option value="DT">DT</option>
                <option value="AGIN"></option>
              </select>
            </div>
            <div id="transfer-target-credit" class="balance-txt"></div>
          </div>
        </div>
        <span id="j-change" class="icon icon_transfer02"></span>
      </div>
      <div class="layout_form layout_form03">
        <form action="#">
          <div class="form_field_warp">
            <div class="form_field">
              <span class="form_field_label"></span>
              <div class="form_field_input"><input type="text" id="transfer-money" placeholder=""></div>
            </div>
          </div>
        </form>
      </div>
      <div class="btn_wrap">
        <a href="javascript:;" id="transfer-submit" class="btn btn01"></a>
      </div>
    </div>
  </div>
</template>
<script>
    export default {
        data() {
            return {};
        },
        props:{},
        methods:{},
        computed:{}
    }
</script>
<style>
</style>



