<template>
  <div class="layout_form layout_form03">
    <div class="form_field_warp">
      <div class="form_field with_icon_label">
        <span class="form_field_label"><span class="icon icon_user"></span></span>
        <div class="form_field_input"><input type="text"  v-model="data.account"  placeholder="(a_()3-15)"></div>
      </div>
      <div class="form_field with_icon_label form_field_password with_right_label">
        <span class="form_field_label"><span class="icon icon_password"></span></span>
        <div class="form_field_input"><input type="password" v-model="data.password" placeholder="&nbsp;&nbsp;&nbsp;(6-16" ></div>
        <span class="right_label"></span>
      </div>
      <div class="form_field with_icon_label">
        <span class="form_field_label"><span class="icon icon_user_file"></span></span>
        <div class="form_field_input"><input type="text" v-model="data.name" placeholder="()" ></div>
      </div>
      <div class="form_field with_icon_label">
        <span class="form_field_label"><span class="icon icon_website"></span></span>
        <div class="form_field_input"><input type="text"  v-model="data.url"  placeholder="(1-6,)"></div>
      </div>
      <div class="form_field with_icon_label">
        <span class="form_field_label"><span class="icon icon_email"></span></span>
        <div class="form_field_input"><input type="text"  v-model="data.email" placeholder="()" ></div>
      </div>
      <div class="form_field with_icon_label">
        <span class="form_field_label"><span class="icon icon_mobile"></span></span>
        <div class="form_field_input"><input type="text"  v-model.number="data.phone" placeholder="()" ></div>
      </div>
      <div class="form_field with_icon_label">
        <span class="form_field_label"><span class="icon icon_qq"></span></span>
        <div class="form_field_input"><input type="text" v-model.number="data.qq" placeholder="QQ/SKYPE()"></div>
      </div>
      <div class="form_field with_icon_label">
        <span class="form_field_label"><span class="icon icon_wechat"></span></span>
        <div class="form_field_input"><input type="text" v-model="data.wechat" placeholder="()" ></div>
      </div>
      <div class="form_field with_icon_label">
        <span class="form_field_label"><span class="icon icon_mobile"></span></span>
        <div class="form_field_input"><input type="text" v-model="data.partner"  placeholder="" id="agent-partner"></div>
      </div>
      <div class="form_field with_icon_label with_right_label form_field_cap_code">
        <span class="form_field_label"><span class="icon icon_cap_code"></span></span>
        <div class="form_field_input"><input v-model="data.imageCode" type="text" placeholder=""  maxlength="4"></div>
        <span class="right_label"><img width="100" height="33"  :src="authImg" @click="getimg"/></span>
      </div>
    </div>
    <div class="btn_wrap">
      <a href="javascript:void(0)" class="btn btn01" @click="register"></a>
    </div>
  </div>
</template>

<script>
  import {getAuthImg,registerAgent} from 'api/authService';
  export default{
    data() {
      return {
        authImg:getAuthImg(),
        data:{
          phone:"", //true string
          email:"", //true string
          name:"", //true string
          account:"", //true string
          imageCode:"", //true string
          password:"", //true string
          qq:"", //true string
          partner:"",
          wechat:"",
          url:""
        }
      }
    },
    methods:{
      checked(){
        let account=/^a_[\da-zA-Z]{3,15}$/;
        let _RegExp=/^(?!(?:\d+|[a-zA-Z]+)$)[\da-zA-Z]{6,16}$/;
        let _phone=/^((13[0-9])|(14[5|7])|(15([0-3]|[5-9]))|(18[0,5-9]))\d{8}$/;
        let email=/^(\w)+(\.\w+)*@(\w)+((\.\w+)+)$/;
        if(!account.test(this.data.account))toast("(a_()3-15)");
        else if(!_RegExp.test(this.data.password))toast("(6-10)");
        else if(this.data.name=="")toast(")");
        else if(this.data.url=="")toast(")");
        else if(! email.test(this.data.email))toast(")");
        else if(!_phone.test(this.data.phone))toast("");
        else if(this.data.imageCode=="")toast("");
        else{
          return true;
        }
      },
      register(){
        if(!this.checked())return;
        registerAgent(this.data).then((res)=>{
          this.getimg()
          if(res.success){
            this.$router.replace("/login/registersuccess");
          }else{
            toast(res.message);
          }
        }).catch(err=>{
          this.getimg()
        });
      },
      getimg(e) {
        this.authImg=getAuthImg();
      },
    }
  }
</script>



