<template>

  <div class="warp-public">
    <form-tip>
      <br>
    </form-tip>
    <div class="form-public">
      <p><input type="text" v-model.number="couponRemit" placeholder="" name="couponRemit"  maxlength="10"><span class="right_label"></span></p>
      <p><select  v-model="couponType" id="couponType">
        <option value="">  </option>
        <option value="PT">PT</option>
        <option value="TTG">TTG</option>
        <option value="NT">NT</option>
        <option value="QT">QT</option>
        <option value="DT">DT</option>
        <option value="MG">MG</option>
      </select></p>
      <p><input type="text" v-model="couponCode" id="couponCode" maxlength="20" placeholder=""></p>
    </div>
    <a href="javascript:void(0);" @click="submit" class="btn btn01" ></a>
</div>
</template>

<script>
  import  "./public.scss"
  import  {transferInforCoupon} from "api/preferential-terms"
  export default {
    data() {
      return {
        couponType:"", // true string
        couponRemit:"", //true string
        couponCode:"",//true string
      };
    },
    props:{},
    computed:{},
    methods:{
      check(){
        if(this.couponRemit=='')toast('');
        else if(this.couponType=='')toast('');
        else if(this.couponCode=='')toast('');
        else{return true; }
      },
      submit(){
        if(!this.check())return;
        console.log(this.$data)
        transferInforCoupon(this.$data).then((data)=>{
          if(data.success){
            this.couponCode='';
          }
          toast(data.message);
        }).catch(()=>{
          toast('');
        })
      }
    },
    created(){
    },
    components:{
      "form-tip": require("./form-tip.vue")
    }
  };
</script>

<style>
</style>



