import {AUTH_NAME} from "@/store/types"//

import  master from "components/master" //
let deposit=(resolve) => resolve(require('views/money/deposit')); //q
let personal=(resolve) => resolve(require('views/money/personal'));//
let transfer=(resolve) => resolve(require('views/money/transfer'));//
let payupdate=(resolve) => resolve(require('views/password/pay-update'));//
let payset=(resolve) => resolve(require('views/password/pay-set'));//
let loginupdate=(resolve) => resolve(require('views/password/login-update'));//
let addBank=(resolve) => resolve(require('views/common/addBank'));  //
export default [{
  path:"/money",
  name:'money',
  component:master,
  redirect:"/money/deposit",
  meta:{
    auth:[AUTH_NAME.USER] //
  },
  children:[
    {
      path:"deposit",
      name:"deposit",
      component:deposit,
      meta:{
        headName:"",
        noCache:true //
      }
    },
    {
      path:"personal",
      name:"money_personal",
      component:personal,
      meta:{
        headName:"",
        noCache:true, //
        auth:[AUTH_NAME.USER,AUTH_NAME.AGENT] //
      }
    },
    {
      path:"transfer",
      name:"transfer",
      component:transfer,
      meta:{
        headName:"",
        noCache:true //
      }
    }
  ]
},
  {
    path:"/pwd",
    name:'pwd',
    component:master,
    meta:{
      auth:[AUTH_NAME.USER,AUTH_NAME.AGENT] //
    },
    children:[
      {
        path:"payset",
        name:"payset",
        component:payset,
        meta:{
          headName:""
          ,noCache:true //
        }
      },
      {
        path:"payupdate",
        name:"payupdate",
        component:payupdate,
        meta:{
          headName:""
          ,noCache:true //
        }
      },
      {
        path:"loginupdate",
        name:"loginupdate",
        component:loginupdate,
        meta:{
          headName:"",
          noCache:true //
        }
      },
    ]
  },
  {
    path:"/datum",
    name:"datum",
    component:master,
    meta:{
      auth:[AUTH_NAME.USER,AUTH_NAME.AGENT] //// -
    },
    children:[
      {
        path:"addbank",
        name:"addbank",
        component:addBank,
        meta:{
          headName:"",
          noCache:true //
        }
      }
    ]
  }
]



