<template>
    <div class="page_content_wrap no_padding user_profile_content">
      <div class="page_content_tip"></div>
      <div class="layout_form sec_box">
        <div class="form_field_warp">
          <div class="form_field">
            <span class="form_field_label">{{userData.accountName}}</span>
            <div class="form_field_input"><input  v-model="Data.accountName" type="text" placeholder="" class="j-accountName">
            </div>
          </div>
          <div class="form_field">
            <span class="form_field_label"></span>
            <div class="form_field_input"><input type="text" :value="userData.phone" readonly></div>
          </div>
          <div class="form_field date_picker with_arrow_icon">
            <span class="form_field_label"></span>
            <div class="form_field_input">
              <input class="date_picker_real" v-model="Data.birthday" type="date"  placeholder="">
            </div>
          </div>
          <div class="form_field">
            <span class="form_field_label"></span>
            <div class="form_field_input">
              <input type="text"  v-model.number="Data.bankno" placeholder=""
                     @input="getBank" maxlength="19"></div>
          </div>
          <div class="cardTips">{{banktxt}}</div>
        </div>
        <div class="form_tip text_align_left">
          ip
          
        </div>
      </div>
      <div class="btn_wrap"><a href="javascript:void(0);" @click="submit" class="btn btn01"></a></div>
    </div>
</template>
<script>
  import {completeUserInfo} from "api/user"
  import {validateBankNo} from "api/safeCenter"
  import { mapGetters } from 'vuex'
  export default {
    name: 'hello',
    data () {
      return {
        banktxt: "",
        validateBankNo: false,
        Data: {
          birthday: "", //true string
          accountName: "", //true string
          bankno: "", //true string
          email: "", //true string
          qq: ""
        }
      }
    },
    methods: {
      checked(){
        if (this.Data.birthday == "") toast("");
        else if (this.Data.accountName == "") toast("");
        else if (this.Data.bankno == "") toast("");
        else if (!this.validateBankNo) toast("");
//        else if (this.Data.email == "") toast("");
//        else if (this.Data.qq == "") toast("QQ");
        else {
          return true;
        }
      },
      getBank(){
        if (/^(\d{16}|\d{19})$/.test(this.Data.bankno)) {
          validateBankNo({bankno: this.Data.bankno}).then(data => {
            if(data.success){
                this.banktxt = data.data;
                this.validateBankNo = data.success;
            }
          })
        } else {
          this.banktxt = "";
        }
      },
      submit(){
        if (!this.checked())return;
        completeUserInfo(this.Data).then(data => {
          if (data.success) {
              this.$store.dispatch("UPDATE_USERDATA");
          } else {
            toast(data.message)
          }
        }).catch(err=>{
          toast("");
        })
      }
    },
    computed: {
      ...mapGetters(["userData"])
    },
  }
</script>



